<template>
  <div class="main">

    <b-img
      :src="require('../assets/logo.png')"
      class="image-fluid"
      alt="Responsive image"
      height="80%"
    ></b-img>

    <div class="cadastro">
      <div class="card">
        <h1>ENTRE AGORA</h1>
        <form v-on:submit.prevent = "enviar">
          <input v-model="form.nome" name="nome" type="text" placeholder="Nome" required />
          <input v-model="form.cpf" name="cpf" type="text" placeholder="CPF" required />
          <input v-model="form.senha" name="senha" type="password" placeholder="Senha" required />
          <input v-model="form.confirmSenha" name="senhaconfirm" type="password" placeholder="Confirmar Senha" required
          />
          <br />
          <b-form-select
            class="mb-3"
            name="cmb_univ"
            nome="universidade"
            v-model="selected_university"
            :options="options_university"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
          ></b-form-select>
          <div class="titulo-radio">Eu sou um:</div>
          <b-form-group v-slot="{ ariaDescribedby }">
            <b-form-radio-group
              class="mb-4"
              :options="options_radio"
              v-model="selected_radio"
              :aria-describedby="ariaDescribedby"
              name="plain-inline"
              plain
            ></b-form-radio-group>
          </b-form-group>
          <button id="button_signup" type="primary">Cadastrar</button><br />
          <br/>
        </form>
        
      </div>

      <div class="link-login">
        Já tem cadastro?
        <router-link to="/" id="_link-login">Login</router-link>
      </div>

    </div>
  </div>
</template>

<script>
import axios from "../service/config.js";
import router from "../router"
export default {
  data() {
    return {
      form:{
        nome: '',
        cpf: '',
        senha: '',
        confirmSenha: '',
      },
      
      selected_university: null,
      selected_radio: "first",
      options_university: [
        { value: null, name: "Selecione sua Universidade" },
        { item: "00000000000001", name: "Potifícia Universidade Católica de Minas Gerais" },
        { item: "00000000000002", name: "Universidade Federal de Minas Gerais" },
        { item: "00000000000003", name: "Centro Universitário UNA" },
      ],
      options_radio: [
        { text: "Aluno", value: "first" },
        { text: "Professor", value: "second" },
      ],

    }
  },

  methods:{
    // POST para realizar o cadastro de alunos e professores.
    enviar:function(){
      console.log("Enviando... to the heeeell");
      
      if (this.form.confirmSenha != this.form.senha){
        alert("As senhas digitadas são diferentes.");
      }else{
        // var new_name = document.getElementsByName("nome").value;
        // console.log(new_name);
        if (this.selected_radio == 'first'){
          axios
          .post('/users/cadastro-aluno', {
            cpfAluno: this.form.cpf,
            senha: this.form.senha,
            cnpjUniversidade: this.selected_university            
          })
          .then(function (response) {
            if (response.data == "Aluno não existe")
                  alert("Este aluno não existe!")
            else if(response.data == "Aluno já cadastrado")
                  alert("Este aluno já está cadastrado!")
            else if(response.data == "Aluno cadastrado com sucesso"){
                alert("Aluno cadastrado com sucesso!")
                router.push('/')
            }
          })
          .catch(function (error) {
            console.log(error);
          });
        }else{

          axios
          .post('/users/cadastro-professor', {  // ESTA URL DO POST PRECISA SER ALTERADA. 
            cpfProfessor: this.form.cpf,
            senha: this.form.senha,
            cnpjUniversidade: this.selected_university
          })
          .then(function (response) {
            if (response.data == "Professor não existe")
                  alert("Este professor não existe!")
            else if(response.data == "Professor já cadastrado")
                  alert("Este professor já está cadastrado!")
            else if(response.data == "Professor cadastrado com sucesso"){
                   alert("Professor cadastrado com sucesso!")
                   router.push('/')
            }
          })
          .catch(function (error) {
            console.log(error);
          });
        }
        
      }
      
    }
  }
  
};

</script>

<style>
.card {
  position: relative;
  transform: translate(0%, 25%);
  background: #f2f2f2;
  border-radius: 20px;
}

.image-fluid {
  position: absolute;
  transform: translate(-49vw, 2vh);
}

.cadastro {
  width: 500px;
  margin: 0 auto;
  font-family: Tahoma, Geneva, sans-serif;
}
.cadastro h1 {
  text-align: center;
  color: #ffbf03;
  font-size: 40px;
  padding: 20px 0 20px 0;
  font-family: "Bebas Neue";
}

.cadastro input[type="password"],
.cadastro input[type="text"] {
  width: 100%;
  padding: 15px;
  border: 1px solid #dddddd;
  margin-bottom: 15px;
  box-sizing: border-box;
}

.cadastro input[name="cpf"] {
  width: 40%;
  margin-left: 5%;
  border-radius: 50px;
  font-family: "Bebas Neue";
  font-size: 20px;
  outline: 0;
}

.cadastro input[name="nome"] {
  width: 40%;
  margin-right: 5%;
  border-radius: 50px;
  font-family: "Bebas Neue";
  font-size: 20px;
  outline: 0;
}

.cadastro input[name="senha"] {
  width: 40%;
  margin-right: 5%;
  border-radius: 50px;
  font-family: "Bebas Neue";
  font-size: 20px;
  outline: 0;
}

.cadastro input[name="senhaconfirm"] {
  width: 40%;
  margin-left: 5%;
  border-radius: 50px;
  font-family: "Bebas Neue";
  font-size: 20px;
  outline: 0;
}

.mb-3 {
  width: 90%;
  height: 5vh;
  border-radius: 5px;
  font-size: 20px;
  font-family: "Bebas Neue";
  background-color: #ffbf03;
  border-color: #ffbf03;
  color: #fff;
  outline: 0;
}

.mb-4 {
  font-family: "Bebas Neue";
  font-size: 2.5vh;
  color: #757575;
}

.titulo-radio {
  font-family: "Bebas Neue";
  font-size: 3vh;
  color: #ffbf03;
}

.link-login {
  position: relative;
  transform: translate(0rem, 9rem);

  font-family: Bebas Neue;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;

  color: #ffffff;
}

#_link-login {
  color: #ffbf03;
}

#button_signup {
  width: 45%;
  white-space: normal;
  height: 6vh;
  font-family: "Bebas Neue";
  letter-spacing: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 27%;
  background: #ffbf03;
  border-radius: 6px;
  cursor: pointer;
  color: #fff;
  border: none;
  font-size: 26px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

@font-face {
  font-family: "Bebas Neue";
  src: url(data:font/truetype;charset=utf-8;base64,)
    format("truetype");
  font-weight: normal;
  font-style: normal;
}

</style>

